body{
    background: rgb(22, 21, 21);

}
.header{
    margin-top: 5rem;
}
.university{
    padding-top: 2rem;
    color: white;
    margin-top: 1rem;
    background: rgb(0 0 36);
    font-family: 'Advent Pro';
}
.sreeram{
    background: rgb(0 0 36);
    margin-top: 1rem;
    margin-left: 0px;
    font-size: 30px;
    justify-content: left;
    margin-left: 1rem;
    padding-bottom: 0.5rem;
    
}
.sreeram>span{
    color: #aed2dd;
    font-family: 'Yellowtail';
}

.table{
    justify-items: left;
    font-size: 24px;
    font-weight: normal;
    color: rgb(7, 140, 163);
    font-family: 'Cinzel', serif;
    
    
}


.insta{
    height: 200px;
    margin-left: 1rem;
    margin-top: 1rem;
}
a:link { text-decoration: none; }
.insta:hover{
    transform: scale(1.02);
}
.instaupper{
    height: 20%;
    background: orange;
}
.instaupper>span{
    color: white;
    font-weight: 700;
    font-style: italic;
    font-size: large;
    
}
.instalower>img{
    width: 350px;
    height: 110px;
    margin-top: 1rem;
    
}
.instalower>span{
    color: rgb(0, 0, 0);
    font-weight: 600;
    font-style: italic;
    font-family: monospace;
    margin-left: 16rem;
}
.instalower{
    height: 80%;
    background: rgb(253, 253, 253);
}
@media screen and (max-width:650px){
    .instalower>img{
        width: 80%;
        height: 60%;
        margin-top: 1rem;
        
    }
    .instalower>span{
        color: rgb(0, 0, 0);
        font-weight: 600;
        font-style: italic;
        font-family: monospace;
        margin-left: 10rem;
    }
    .university{
        margin-left: 4%;
        padding-top: 2rem;
        color: white;
        margin-top: 3rem;
        background: rgb(0 0 36);
        font-family: 'Advent Pro';
    }
    .sreeram{
        background: rgb(0 0 36);
    margin-top: 1rem;
    margin-left: 0px;
    font-size: 30px;
    justify-content: left;
    margin-left: 1rem;
    padding-bottom: 0.5rem;
        
        
    }
    .sreeram>table{
        font-size: 15px;
    }
}