.sreeramachandran{
    font-family: 'Cinzel Decorative';
    letter-spacing: 4px;
    color: rgb(231 205 77);
    font-size: 20px;
    
    text-shadow: 0 0 #ff8100;
    margin-right: 5rem;
}
.dropdown{
    background: white;
    
    border-radius: 3px;
    
    
}
.text-warning{
    margin-right: 3rem;
    
}

@media screen and (max-width:700px) {
    .navbar{
        
            min-height: 80px;
            
            
        
    }
    .toggle{
        align-self: flex-start;
        background-color: none;
    }
    .toggle:hover{
        background: none;
        transform: scale(1.05);
        font-size: large;
        transition: 1s;
    }
    .sreeramachandran{
        font-family: 'Cinzel Decorative';
        letter-spacing: 4px;
        color: rgb(231 205 77);
        font-size: 15px;
        
        text-shadow: 0 0 #ff8100;
        margin-right: 0rem;
    }
}