body{
    
}
.card{
    margin-top: 10rem;
    background: rgb(245, 115, 9);
    margin-bottom: 2rem;
    margin-left: -2rem;
}
.cardspace{
    margin-left: -1rem;
}.cardspace1{
    margin-left: 0rem;
}
.cardspace2{
    margin-left: 1rem;
}
.project>span{
    color: white;
    font-family: ui-monospace;
    background: red;
    border-radius: 10%;
    
    font-weight: bolder;
}
.image{
    height: 300px;
    object-fit: cover;
    
}
.br{
    margin-top: 7rem;
    color: white;
    height: 1px;
    width: 100%;
    background: white;
    
}
@media screen and (max-width:840px) {
    .card{
        margin-left: 4%;
        min-width: 100%;
        min-height: 70%;
        margin-bottom: -4rem;
        margin-top: 0rem;
        transform: scale(0.8);
    }
    .br{
        margin-top: 10rem;
        
    }
    .image{
        min-height: 300px;
        min-width: 100%;
        object-fit: cover;
    }
    .project>span{
        margin-left: 4%;
    }
}