.gallery{
    margin-top: 9rem;
}

.image2>img{
    width: 300px;
    height: 440px;
    object-fit: cover;
     margin-top: 0rem;
     margin-bottom: 2rem;
    
}

.hr{
    margin-top: -1rem;
    color: white;
    height: 1px;
    width: 100%;
    background: white;
    
}
.gallery>span{
    color: white;
    font-family: ui-monospace;
    background: red;
    border-radius: 10%;
    font-weight: bolder;
    
    
}

.image2>img:hover{
    transform: scale(1.1);
    transition: 1s;
    opacity: 0.7;
}

@media screen and (max-width:840px) {
    
    .image2>img{
        width: 300px;
        height: 440px;
        object-fit: cover;
         margin-top: -4rem;
         margin-left: 4%;
         margin-bottom: 6rem;
    }
    
    
}
