.testimg>img{
    height: 150px;
    width: 150px;
    border-radius: 50%;
    margin-top: 10rem;
    margin-bottom: 3rem;
    object-fit:cover ;
    object-position: 0 0.1px;
}
.quote{
    color: aliceblue;
   
   
    font-size: 18px;
    font-weight: normal;
    font-family: inherit;
    margin-bottom: 2rem;
}
.auther{
    color: #a9cec2;
    
    font-family: 'Caveat';
    margin-bottom: 5rem;
}

@media screen and (max-width:1201px) {
    .testimg{
        margin-left: 4%;
    }
    .quote{
        color: aliceblue;
        margin-left: 4%;
       
        font-size: 18px;
        font-weight: normal;
        font-family: 'cursive';
        margin-bottom: 2rem;
        
    }
    .auther{
        color: #a9cec2;
        margin-left: 4%;
        font-family: 'Caveat';
        margin-bottom: 5rem;
    }
   
    
}